/* CSS web fonts, font-family, font-style, font-weight, etc. */

html, body {
    font-family: "Segoe UI","Segeo WP",Tahoma,Arial,"Helvetica Neue",Helvetica,sans-serif;
}

code, kbd, pre, samp {
    font-family: Consolas,Menlo,Monaco,"Lucida Console","Liberation Mono","DejaVu Sans Mono","Bitstream Vera Sans Mono","Courier New",monospace,sans-serif;
}

@font-face {
    font-family: 'icons';
    src: url("../fonts/docs_icons.eot");
    src: url("../fonts/docs_icons.eot") format("embedded-opentype"),url("../fonts/docs_icons.ttf") format("truetype"),url("../fonts/docs_icons.svg") format("svg");
    font-weight: normal;
    font-style: normal
}

[class^="icon-"],[class*=" icon-"] {
    font-family: 'icons' !important;
    speak: none;
    font-style: normal;
    font-weight: bold;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: baseline;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/*
disable web fonts temporarily for the following reasons:
1. current monolithic CSS approach would block page content rendering until the web fonts get loaded, it's a huge experience downside for slow networks
